html,
body {
  height: 100%;
}

.container {
  max-width: 1200px;
  margin: 0px auto;
}

.wrapper {
  min-height: 100%;
  overflow: hidden;

  display: flex;
  flex-direction: column;
}

.header {
  border: 5px solid red;
}

.main {
  flex: 1 1 auto;
  border: 5px solid green;
}

.footer {
  border: 5px solid blue;
}
